import App from './app';
import { toggleService } from './appState';
import { Auth } from './classes/Auth';
import FoodItem from './classes/FoodItem';
import Store from './classes/Store';
import UI from './classes/UI';
import { openScreen, closeScreen } from './components/FoodScreen';

// App functionality
const app = () => {
	// Open menu
	document.querySelector('#add').onclick = () => {
		document.querySelector('.menu-bar').classList.toggle('active-bar');
	}

	//#region Authentication & Modal
	function closeModal(modal){
		document.getElementById(modal).style.display = "none";
		currentOpen = 'none'
	}

	function switchModal(curModal, modal){
		closeModal(curModal);
		document.getElementById(modal).style.display = "block";
		currentOpen = modal;
	}

	let currentOpen = 'none';
	let closeButtons = document.querySelectorAll('.close');
	let switchButtons = document.querySelectorAll('.switch-auth');

	// Open sidebar
	document.querySelector('#menu').onclick = () => {
		document.getElementById("sidebar").style.width = "250px"
	}
	
	// Open Login
	document.querySelector('#login').onclick = () => {
		document.getElementById('login-modal').style.display = "block";
		currentOpen = 'login-modal';
	}
	// Open Login
	document.querySelector('#signup').onclick = () => {
		document.getElementById('signup-modal').style.display = "block";
		currentOpen = 'signup-modal';
	}

	// Close sidebar
	document.querySelector('#close-sidebar').onclick = () => {
		document.getElementById('sidebar').style.width = "0";
	}

	// Close login
	for (let i = 0; i < closeButtons.length; i++) {
		closeButtons[i].addEventListener('click', function(){
			console.log(currentOpen);
			if(currentOpen === 'login-modal'){
				closeModal('login-modal')
			} else if(currentOpen === 'signup-modal'){
				closeModal('signup-modal')
			}
		});
	}

	// Close signup
	for (let i = 0; i < switchButtons.length; i++) {
		switchButtons[i].addEventListener('click', function(){
			console.log(currentOpen);
			if(currentOpen === 'login-modal'){
				switchModal(currentOpen, 'signup-modal')
			} else if(currentOpen === 'signup-modal'){
				switchModal(currentOpen, 'login-modal')
			}
		});
	}
	//#endregion

	// Open food adding screen
	document.querySelector('#add-type').onclick = () => {
		document.querySelector('.menu-bar').classList.toggle('active-bar');
		openScreen('food-add-screen');
		document.getElementById('apply').style.display = 'block';
		toggleService.send('TOGGLE');
	}
	// Close food adding screen
	document.querySelector('#close-add-screen').onclick = () => {
		closeScreen('food-add-screen');
		document.getElementById('apply').style.display = 'none';
		toggleService.send('CLOSE');
	}
	// Display FoodItems
	document.addEventListener('DOMContentLoaded', UI.displayFood);

	// Add FoodItem
	document.querySelector('#apply').addEventListener('click', (e) => {
		const name = document.querySelector('#food-name-input').value;
		const date = document.querySelector('#food-date-input').value;
		const notification = document.querySelector('#food-ntfc-input').value;

		// Validate 
		if(name === '' || date === '' || notification === ''){
			UI.showAlert('Please fill in all fields', 'error');
		} else {
			// Instantiate book
			const food = new FoodItem(name, date, notification);
			console.log(food);

			// Add Food UI
			UI.addFoodToList(food);
			Store.addFood(food);

			// Close screen and clear field 
			UI.clearFields();
			closeScreen('food-add-screen');
			document.getElementById('apply').style.display = 'none';
			toggleService.send('CLOSE');

			// Show alert
			UI.showAlert('Food added', 'success');
		}
	});

	// Remove FoodItem
	document.querySelector('#app-containers').addEventListener('click', (e) => {
		console.log(e.target);
		UI.deleteFood(e.target);
		Store.removeFood(e.target.parentElement.parentElement.id);
	});
}

// Show app
const display = async (cb) => {
	const response = await App()
	document.getElementById('app').appendChild(response);
	Auth();
	cb(response);
};

display(app)