const FoodScreen = () => {

	const template = `
	<!-- Food Adding Screen -->
	<div class="food-add-screen" id="food-add-screen">
		<span class="close" id="close-add-screen">&times;</span>
			<div class="add-food-section">
				<h2 class="add-food-heading">Add Food</h2>
				<input type="text" id="food-name-input" class="food-name-input" placeholder="Food" value="">

				<div class="wraper">
					<h3>Date</h3>
					<p>Set Date</p>
					<input type="date" id="food-date-input" class="food-input active-input">
				</div>

				<div class="wraper">
					<h3>Notifications</h3>
					<p>Set Notifications</p>
					<input type="date" id="food-ntfc-input" class="food-input active-input">
				</div>
			</div>
	</div>
  `;

	return template;
};

export function openScreen(screen){
	document.getElementById(screen).style.display = "block";
}

export function closeScreen(screen){
	document.getElementById(screen).style.display = "none";
}

export default FoodScreen;