import UI from "./UI"
import Store from './Store'

//Listen To User Authentication Change
export function Auth(){

	var firebaseConfig = {
		apiKey: "AIzaSyDUmCp2oLBGYdtuHPI163HbpGS9-lHIYM0",
		authDomain: "exofood-a3a95.firebaseapp.com",
		projectId: "exofood-a3a95",
		storageBucket: "exofood-a3a95.appspot.com",
		messagingSenderId: "321188316683",
		appId: "1:321188316683:web:abad3c2f47b5b7f54d690e",
		measurementId: "G-1DHMDF5N34"
	};

	firebase.initializeApp(firebaseConfig);
	firebase.analytics();

	const auth = firebase.auth()
	const db = firebase.firestore()
	
	// Sign Up
	const signupForm = document.querySelector('#signup-form')
	
	signupForm.addEventListener('submit', (e) => {
		e.preventDefault()
		//Get user info
		const email = signupForm['signup-email'].value
		const password = signupForm['signup-password'].value
		//Sign up the user
		auth.createUserWithEmailAndPassword(email, password).then(cred => {
			db.collection('users').doc(cred.user.uid).set({
				name: signupForm['signup-name'].value
			})
		}).then(() => {
			// Close Modal
			const modal = document.querySelector('#signup-modal');
			modal.style.display = "none";
		})
	})
	
	// Log In
	const loginForm = document.querySelector("#login-form");
	loginForm.addEventListener('submit', () => {
		// Get user info
		const email = loginForm['login-email'].value;
		const password = loginForm['login-password'].value;
		// Login the user
		auth.signInWithEmailAndPassword(email, password).then(() => {
			// Close Modal
			const modal = document.querySelector('#login-modal');
			modal.style.display = "none";
		})
	})
	
	// Log Out
	const logOut = document.querySelector('#logout');
	logOut.addEventListener('click', (e) => {
		e.preventDefault();
		localStorage.clear();
		UI.clearUI();
		auth.signOut();
	})

	auth.onAuthStateChanged(user => {
		if (user) {
			UI.setupUI(user, db);
			Store.setUser(user, db);
			Store.syncDatabase();
		} else {
			UI.setupUI();
		}
	})


}