import { dateDiffIndays } from "./FoodItem";
import Store from "./Store";

class UI{

	static async setupUI(user, db){
		if (user) {
			//Toggle UI Elements
			document.querySelector('.account-register').style.display = 'none';
			document.querySelector('.account-block').style.display = 'flex';
			db.collection('users').doc(user.uid).get().then(doc => {
				const accountName = document.querySelector('.account-name');
				const accountInfoName = document.querySelector('.account-info-name');
				const accountInfoEmail = document.querySelector('.account-info-email');
				accountName.innerHTML = doc.data().name;
				accountInfoName.innerHTML = doc.data().name;
				accountInfoEmail.innerHTML = user.email;
			})
		} else {
			document.querySelector('.account-register').style.display = 'flex';
			document.querySelector('.account-block').style.display = 'none';
		}
	}
	
	static displayFood(){
		const StoredFood = Store.getFood();

		const food = StoredFood;
		food.forEach((food) => UI.addFoodToList(food));
	}

	static addFoodToList(food){
		let currentDate = new Date().toJSON().slice(0, 10).replace(/-/g, '-')
		let daysUntilExpired = dateDiffIndays(currentDate, food.date);
		let daysUntil;

		switch(daysUntilExpired){
			case 1 || -1:
				daysUntil = daysUntilExpired + ' Day'
				break;
			default:
				daysUntil = daysUntilExpired + ' Days'
				break;
		}

		const template = `
		<div class="food-block" id=${food.id}>
			<h2 class="food-name" id="food-name">${food.name}</h2>
			<p class="close" id="close"><i class="fas fa-times delete-food"></i></p>
			<p class="p" id="days-untill">${daysUntil}</p>
			<p class="food-date p" id="food-date">${food.date}</p>
		</div>
		`

		if(daysUntilExpired <= 0){
			document.getElementById('food-expired').innerHTML += template;
		} else if (daysUntilExpired <= 3 && daysUntilExpired > 0){
			document.getElementById('food-soon').innerHTML += template;
		} else {
			document.getElementById('food-list').innerHTML += template;
		}
	}
	
	static showAlert(message, className){
		const template = `
			<div class="alert ${className}"> ${message} </div>
		`
		document.getElementById('alert-area').style.display = 'block';
		document.getElementById('alert-area').innerHTML += template;

		// Vanish in 3s
		setTimeout(() => {
			document.querySelector('.alert').remove()
			document.getElementById('alert-area').style.display = 'none';
		}, 3000);
	}

	static deleteFood(el){
		if(el.classList.contains('delete-food')){
			document.getElementById(el.parentElement.parentElement.id).remove();
			this.showAlert('Removed', 'success');
		}
	}

	static clearFields(){
		document.querySelector('#food-name-input').value = '';
		document.querySelector('#food-date-input').value = '';
		document.querySelector('#food-ntfc-input').value = '';
	}

	static clearUI(){
		document.getElementById('food-expired').innerHTML = "";
		document.getElementById('food-soon').innerHTML = "";
		document.getElementById('food-list').innerHTML = "";
	}
}

export default UI;