const Menu = () => {

	const template = `
	<!-- Menu Bar On Bottom -->
	<div class="menu-bar">
		<div class="menu" id="menu">
			<i class="fas fa-bars"></i>
		</div>
		<button class="add-data" id="add-data"><i class="fas fa-search"></i></button>
		<button class="add-type" id="add-type">+</button>
		<button class="add-image" id="add-image"><i class="fas fa-camera"></i></button>
		<button class="add" id="add"><span>+</span></button>
		<button class="add" id="apply"><span>&checkmark;</span></button>
	</div>
  `;

	return template;
};

export default Menu;