import UI from "./UI";

class Store{
	static setUser(user, db){
		this.user = user;
		this.db = db;
	}

	static getFood(){
		let food;

		if(localStorage.getItem('foodItems') === null){
			food = [];
		} else {
			food = JSON.parse(localStorage.getItem('foodItems'));
		}

		return food;
	}

	static addFood(food){
		const foodItems = Store.getFood();
		foodItems.push(food);
		localStorage.setItem('foodItems', JSON.stringify(foodItems));

		this.db.collection('users').doc(this.user.uid).update({
			"foodItems": Store.getFood()
		});
	}

	static removeFood(id){
		const foodItems = Store.getFood();

		foodItems.forEach((food, index) => {
			if(food.id === id){
				foodItems.splice(index, 1);
			}
		});

		localStorage.setItem('foodItems', JSON.stringify(foodItems));

		this.db.collection('users').doc(this.user.uid).update({
			"foodItems": Store.getFood()
		});
	}

	static syncDatabase(){

		function mapFromArray(array, prop) {
			var map = {};
			for (var i=0; i < array.length; i++) {
					map[ array[i][prop] ] = array[i];
			}
			return map;
		}
		
		function isEqual(a, b) {
			return a.title === b.title && a.type === b.type;
		}
		
		function getDelta(o, n, comparator)  {
			var delta = {
					added: [],
					deleted: [],
					changed: []
			};
			var mapO = mapFromArray(o, 'id');
			var mapN = mapFromArray(n, 'id'); 
			for (var id in mapO) {
					if (!mapN.hasOwnProperty(id)) {
							delta.deleted.push(mapO[id]);
					} else if (!comparator(mapN[id], mapO[id])){
							delta.changed.push(mapN[id]);
					}
			}
			
			for (var id in mapN) {
					if (!mapO.hasOwnProperty(id)) {
							delta.added.push( mapN[id] )
					}
			}
			return delta;
		}

		this.db.collection('users').doc(this.user.uid).get().then(doc => {
			let dbFood = doc.data().foodItems;
			let localFood = this.getFood();

			console.log("Firebase : ", dbFood);
			console.log("Local : ", localFood);

			// Compare local with db (Add to localstorage)
			let addedFromDB = getDelta(localFood, dbFood, isEqual).added;
			for (let i = 0; i < addedFromDB.length; i++) {
				this.addFood(addedFromDB[i]);
				UI.addFoodToList(addedFromDB[i]);
			}

			// Compare db with local (Add to Firestore)
			let addedFromLocal = getDelta(dbFood, localFood, isEqual).added;
			for (let i = 0; i < addedFromLocal.length; i++) {
				console.log(addedFromLocal[i]);
				this.db.collection('users').doc(this.user.uid).update({
					"foodItems": localFood
				});
			}

		});
	}
}

export default Store;